.container {
  /* border: 2px solid red; */
  width: 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #3b3a3a;
  text-align: center;
}
.container h1 {
  font-weight: 800;
  margin-top: 20px;
  margin-bottom: 20px;
}
.input-field {
  width: 30rem;
  background-color: #fff;
  border-radius: 5px;
  border: 1px;
 
}
.button-add {
  padding: 15px 20px !important;
  background-color: #3b3a3a !important;
   margin-left: 10px !important;
   margin-bottom: 30px !important;
}
.list{
 
  text-align: left;
  border: 1px solid;
  background-color: #fff;
  margin-top: 5px;
  padding: 10px;
  width: 35.2rem;
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  gap: 10px;
}
.list div{
flex-grow: 1;
text-align: right;
/* border: 2px solid red; */
}
.strike{
  
  text-decoration: line-through;
  
}


/* check box css */


.b-contain *, .b-contain *::before, .b-contain *::after {
	box-sizing: content-box !important;
}

.b-contain input {
	position: absolute;
	z-index: -1;
	opacity: 0;
}

.b-contain {
	display: table;
	position: relative;
	padding-left: 1.8rem;
	cursor: pointer;
	margin-bottom: .5rem;
}

.b-contain input[type="checkbox"] ~ .b-input {
	position: absolute;
	top: -.4rem;
	left: 0;
	height: 1.25rem;
	width: 1.25rem;
	background: rgba(241, 245, 248, 1);
	transition: background 250ms;
	border: 2px solid rgb(13, 13, 14);
	border-radius: 0.125rem;
}


.b-contain input[type="checkbox"] ~ .b-input::after {
	content: '';
	position: absolute;
	display: none;
	left: .45rem;
	top: .18rem;
	width: .25rem;
	height: .6rem;
	border: solid #fff;
	border-width: 0 2px 2px 0;
	transition: background 250ms;
	transform: rotate(45deg);
}



.b-contain input:checked ~ .b-input::after {
	display: block;
}

.b-contain:hover input ~ .b-input,
.b-contain input:focus ~ .b-input {
	background: rgb(231, 238, 243);
}


.b-contain input:checked ~ .b-input {
	background: rgb(6, 213, 23);
	border-color: rgb(13, 14, 13);
}





.b-contain input:checked:focus ~ .b-input, .b-contain:hover input:not([disabled]):checked ~ .b-input {
	background: rgb(22, 241, 7);
	border-color: rgb(11, 11, 11);
}

.b-contain .b-input::before {
	content: '';
	display: block;
	position: absolute;
	left: 0;
	top: 0;
	width: 3rem;
	height: 3rem;
	margin-left: -0.85rem;
	margin-top: -0.85rem;
	background: rgb(7, 232, 90);
	border-radius: 2rem;
	opacity: .6;
	z-index: 99999;
	transform: scale(0);
}




/*check box css ends */

@media only screen and (max-width: 768px) {
  .container {
    width: 100vw;
  }
  .input-field {
    width: 90vw;
    padding: 20px;
  }
  .button-add {
    padding: 5px 8px !important;
    background-color: #3b3a3a !important;
    margin-bottom: 10px !important;
    margin-top: 10px !important;
  }
  .list{
    width: 80vw;
    flex-wrap: wrap;
  }
}





